(function() {
  'use strict';

  angular
    .module('site', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ui.router', 'ui.bootstrap', 'toastr']);

})();

(function() {
  'use strict';

  angular
    .module('site')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, toastr) {
    var vm = this;

    vm.message = 'Welcome';

    vm.showToastr = function () {
      toastr.info('Watchout!');
      vm.classAnimation = '';
    }

  }
})();

(function() {
  'use strict';

  angular
    .module('site')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('site')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

/* global malarkey:false, moment:false */
(function() {
  'use strict';

  angular
    .module('site')
    .constant('malarkey', malarkey)
    .constant('moment', moment);

})();

(function() {
  'use strict';

  angular
    .module('site')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;
  }

})();

angular.module("site").run(["$templateCache", function($templateCache) {$templateCache.put("app/main/main.html","<div class=\"container\"><div class=\"jumbotron\"><h2 class=\"text-primary\"><i class=\"ion-flag\"></i> Hari\'s Projects</h2><p></p><h4>Happy 2016, wishing you all a very prosperous and healthy year ahead!</h4><p ng-click=\"main.showToastr()\">Coming soon, watch out for my projects here</p><p></p></div><p class=\"text-muted\">Site Generated by awsome <a href=\"https://github.com/Swiip/generator-gulp-angular\">generator gulp angular</a> and powered by <strong>Github Pages <strong></strong></strong></p></div>");}]);